@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital@1&display=swap);
*{
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.inputBox{
    display:grid;
    place-items: center;
    height:100vh;
    background-color: #f8f8f8;
}
.inputBox_field{
    padding: 10%;
    text-align: center;
    background-color: white;
    border-radius: 10px;
    box-shadow:0 5px 6px rgba(0,0,0,0.12),0 2px 3px rgba(0,0,0,0.24);
    padding-top: 0;
    padding-bottom: 50px;
    margin: 20px;
}
.inputBox_field>form>button{
    margin-top: 30px;
    text-transform: inherit i !important;
    background-color: #0a8d48 !important;
    color: white;
}
.inputBox_field>img{
    width:15%;
    padding-bottom: 40px;
    padding-top: 20px;
}

.inputBox_field>h1{
    font-family: 'Montserrat', sans-serif;
    margin-top: 10px;
};





.result{
    height:100vh;
    display: grid;
    place-items: center;
}
.result_box >Button{
    background-color:white !important;
}
.result_box{
    padding: 10%;
    text-align: center;
    background-color: #3b9e28;
    border-radius: 10px;
    box-shadow:0 5px 6px rgba(0,0,0,0.12),0 2px 3px rgba(0,0,0,0.24);
    padding-top: 50px;
    padding-bottom: 50px;
    
};

